@import "mixins";
@import "colors";
@import "blog-post";

.blog {
	&-header {
		padding: 4rem 0;
		display: flex;
		align-items: center;
		justify-content: center;

		font-size: 2rem;
		text-transform: uppercase;
		letter-spacing: 0.75rem;
		font-weight: 400;
		color: $white;
	}

	&-entry {
		padding: 2rem;
		padding-top: 3rem;
		padding-bottom: 3rem;

		cursor: pointer;

		.blog-title {
			p {
				font-size: 1.2rem;
				line-height: 2;
			}
		}

		&:hover {
			.featured-img img {
				transform: scale(1.035);
				opacity: 1;
			}
		}

		.featured-img {
			box-shadow: 0 0 1rem 0 rgba($color: #000000, $alpha: 0.5);

			img {
				transition-timing-function: ease-in-out;
				transition-duration: 0.15s;
				opacity: 0.75;
			}
		}
	}
}