.title {
    color: $white;
	text-align: center;
    padding-top:3rem;
    padding-bottom:2rem;
	line-height:1.5rem;

    @include breakpoint-up(medium){    
        padding-top:4rem;
        padding-bottom:4rem;
    }

    h1 {
        text-transform: uppercase;
        font-size: 2rem;
        margin-bottom:0rem;
		line-height:2.5rem;
    }

    h2 {
        text-transform: capitalize;
        font-size:1.3rem;
		line-height:2.5rem;
        color:$dsblue;
        margin:0;
    }

    h3 {
        font-size:1.1rem;
		line-height:2.5rem;
        letter-spacing:1.2rem;
        text-transform: uppercase;
    }
}
